import { Params } from "../../src/Params";
import { AnimatedPointSet } from "../../src/AnimatedPointSet";

const animatedPointSet = new AnimatedPointSet();
const animatedPointSetBg = new AnimatedPointSet();

const params = new Params([
    {
        maxPoints:
            { type: "number", min: 3, max: 1000, step: 1, initial: 100, randomize: false },
    },
    {
        connectivityType: {
            type: "variant",
            options: ["DELAUNAY", "COMPLETE_GRAPH"],
            initial: "DELAUNAY",
        },
        motionType: {
            type: "variant",
            options: ["OFFSET_CHANGE", "VELOCITY_CHANGE", "DIRECTION_CHANGE"],
            initial: "DIRECTION_CHANGE",
        }
    },
    {
        maxOffset: { type: "number", min: 0, max: 0.1, step: 0.001, initial: 0.05 },
        maxVelocity: { type: "number", min: 0, max: 0.05, step: 0.001, initial: 0.025 },
    },
    {
        overscaleFactor: { type: "number", min: 1, max: 4, step: 0.01, initial: 1, randomize: true },
        strokeWidth: { type: "number", min: 0.1, max: 5, step: 0.001, initial: 0.25 },
        strokeColor: { type: "color", initial: "rgba(255, 255, 255, 1)" },
        background: { type: "color", initial: "rgba(0, 0, 0, 1)" },
    },
    {
        mapLineLengthToProps: { type: "boolean", initial: true },
        minOpacity: { type: "number", min: 0, max: 1, step: 0.001, initial: 0 },
        maxOpacity: { type: "number", min: 0, max: 1, step: 0.001, initial: 1 },
        opacityCutoffLength: { type: "number", min: 0, max: 1, step: 0.001, initial: 0.1 },
    },
    {
        conformToShape: {
            type: "variant",
            options: ["NONE", "CIRCLE", "RECTANGLE"],
            initial: "NONE",
        },
        conformCircleRadius:
            { type: "number", min: 0, max: 0.5, step: 0.001, initial: 0.1, },
        conformCirclePower:
            { type: "number", min: 0, max: 4, step: 0.001, initial: 0.5, },
        conformCircleStickiness:
            { type: "number", min: 0, max: 0.1, step: 0.001, initial: 0.05, },
        conformCircleCenterX:
            { type: "number", min: 0, max: 1, step: 0.001, initial: 0.5, randomize: true, },
        conformCircleCenterY:
            { type: "number", min: 0, max: 1, step: 0.001, initial: 0.5, randomize: true, },
        conformCutStickyVertices:
            { type: "boolean", initial: true, },
    }
]);

const seed = "1600407563912";

params.randomize(seed, animatedPointSet.params);
animatedPointSet.params.maxOffset = 0;
animatedPointSet.params.overscaleFactor = 1;
animatedPointSet.params.conformCircleCenterX = 0.5;
animatedPointSet.params.conformCircleCenterY = 0.5;
animatedPointSet.params.conformCircleStickiness = 0.04;
animatedPointSet.params.strokeWidth = 2;
animatedPointSet.randomize(seed);

params.randomize(seed, animatedPointSetBg.params);
animatedPointSetBg.params.maxOffset = 0;
animatedPointSetBg.params.overscaleFactor = 1.5;
animatedPointSetBg.params.conformToShape = "NONE";
animatedPointSetBg.params.connectivityType = "DELAUNAY";
animatedPointSetBg.params.background = "rgba(0, 0, 0, 0)";
// animatedPointSetBg.params.strokeColor = "rgba(196, 122, 73, 1)";
animatedPointSetBg.params.strokeColor = animatedPointSet.params.strokeColor;
animatedPointSetBg.params.strokeWidth = 0.5;
animatedPointSetBg.randomize(seed);

console.log(animatedPointSet.params.strokeColor);

const canvas = document.querySelector("canvas");
const offscreenCanvas = document.createElement("canvas");

const context = canvas.getContext("2d");
const offscreenContext = offscreenCanvas.getContext("2d");

offscreenContext.font = "20px serif";
const textDeltaX = offscreenContext.measureText("demo001").width * 1.3;

const resizeCanvas = () => {
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    canvas.style.width = window.innerWidth + "px";
    canvas.style.height = window.innerHeight + "px";
    // offscreenCanvas.width = canvas.width;
    // offscreenCanvas.height = canvas.height;
    // // offscreenContext.clearRect(0, 0, canvas.width, canvas.height);
    // offscreenContext.fillStyle = animatedPointSet.state.strokeStyle;
    // for (let y = 10; y < canvas.height + 60; y += 30) {
    //     for (let x = ((y - 10) % 60 === 0 ? -25 : 0); x < canvas.width; x += textDeltaX) {
    //         offscreenContext.font = "20px serif";
    //         offscreenContext.fillText("demo001", x, y);
    //     }
    // }
}

const MIN_FONT_ALPHA = 0.6;

const theBeginningOfTime = Date.now();

let isCursorInsideCircle = false;
let lastCursorEntryTimestamp = theBeginningOfTime;
let lastCursorExitTimestamp = theBeginningOfTime;
let currentFontAlpha = 0;
let lastMinFontAlpha = 1;
let lastMaxFontAlpha = MIN_FONT_ALPHA;

const tick = () => {
    const w = context.canvas.width;
    const h = context.canvas.height;
    const now = Date.now();
    const globalTime = (now - theBeginningOfTime) / 1000;
    const cursorStatusTime = isCursorInsideCircle
        ? (now - lastCursorEntryTimestamp) / 1000
        : (now - lastCursorExitTimestamp) / 1000;
    context.fillStyle = "rgba(196, 122, 73, 1)";
    context.fillRect(0, 0, w, h);
    context.fillStyle = animatedPointSet.state.strokeStyle;
    // context.globalAlpha = 0.1;
    // animatedPointSetBg.draw(context);
    // context.globalAlpha = 1;
    context.save();
    currentFontAlpha = isCursorInsideCircle
        ? Math.max(MIN_FONT_ALPHA, lastMaxFontAlpha - cursorStatusTime)
        : Math.min(1, Math.max(lastMinFontAlpha, cursorStatusTime));
    // currentFontAlpha = Math.abs(currentFontAlpha - potentialFontAlpha) > 0.1
    //     ? (currentFontAlpha + potentialFontAlpha) / 2
    //     : potentialFontAlpha;
    context.globalAlpha = currentFontAlpha;
    // context.translate(0, (-globalTime * 40) % 60);
    // context.drawImage(offscreenCanvas, 0, 0);
    let textRowIndex = 0;
    for (let y = 10 + (-globalTime * 20) % 60; y < h + 30; y += 30) {
        const offsetX = textRowIndex % 2 === 0 ? -25 : 0;
        for (let x = offsetX; x < w; x += textDeltaX) {
            context.font = "20px serif";
            context.fillText("demo001", x, y);
        }
        textRowIndex++;
    }
    // animatedPointSetBg.state.strokeStyle = animatedPointSet.state.strokeStyle;
    context.restore();
    context.beginPath();
    context.arc(
        animatedPointSet.params.conformCircleCenterX * w,
        animatedPointSet.params.conformCircleCenterY * h,
        animatedPointSet.params.conformCircleRadius * Math.min(w, h),
        0, 2 * Math.PI, false);
    context.save();
    context.clip();
    animatedPointSet.params.conformCirclePower = isCursorInsideCircle
        ? 0.525 + 0.05 * (0.5 + Math.sin(2 * cursorStatusTime - 0.5 * Math.PI) / 2)
        : 0.525;
    animatedPointSet.draw(context);
    context.restore();
    requestAnimationFrame(tick);
};

resizeCanvas();
tick();

window.addEventListener("resize", () => {
    resizeCanvas();
    animatedPointSet.draw(context);
});

canvas.addEventListener("mousemove", (event) => {
    const dx = event.offsetX - animatedPointSet.params.conformCircleCenterX * canvas.width;
    const dy = event.offsetY - animatedPointSet.params.conformCircleCenterY * canvas.height;
    const distance = Math.sqrt(dx * dx + dy * dy);
    const denormRadius = animatedPointSet.params.conformCircleRadius * Math.min(canvas.width, canvas.height);
    if (distance < denormRadius) {
        if (!isCursorInsideCircle) {
            isCursorInsideCircle = true;
            lastCursorEntryTimestamp = Date.now();
            lastMaxFontAlpha = currentFontAlpha;
            document.body.style.cursor = "pointer";
        }
    } else if (isCursorInsideCircle) {
        isCursorInsideCircle = false;
        lastCursorExitTimestamp = Date.now();
        lastMinFontAlpha = currentFontAlpha;
        document.body.style.cursor = "initial";
    }
});

canvas.addEventListener("click", () => {
    if (isCursorInsideCircle) {
        const seed = Date.now().toString();
        console.log(seed);
        animatedPointSet.randomize(seed);
    }
});
